.MapBody {
    height: 92vh;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    /* justify-content: space-around; */
    padding: 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

#map {
    height: 95%;
    width: 80%;
    border: #3FE7B1 3px solid;
    border-radius: 10px;
    -webkit-box-shadow: 2px 5px 15px #46B8DA;
            box-shadow: 2px 5px 15px #46B8DA;
}

.sidebar {
    height: 95%;
    width: 20%;
    border: black 2px solid;
    overflow: hidden;
    border-right: 1px solid rgba(0, 0, 0, 0.25);
}

h1 {
    font-size: 22px;
    margin: 0;
    font-weight: 400;
    line-height: 20px;
    padding: 20px 2px;
}
  
a {
    color: #404040;
    text-decoration: none;
}
  
a:hover {
    color: #101010;
}
  
.heading {
    background: #fff;
    border-bottom: 1px solid #eee;
    height: 60px;
    line-height: 60px;
    padding: 0 10px;
}
  
.listings {
    height: 100%;
    overflow: auto;
    padding-bottom: 60px;
}
  
.listings .item {
    display: block;
    border-bottom: 1px solid #eee;
    padding: 10px;
    text-decoration: none;
}
  
.listings .item:last-child { border-bottom: none; }
  
.listings .item .title {
    display: block;
    color: #00853e;
    font-weight: 700;
}
  
.listings .item .title small { font-weight: 400; }
  
.listings .item.active .title,
.listings .item .title:hover { color: #8cc63f; }
  
.listings .item.active {
    background-color: #f8f8f8;
}

::-webkit-scrollbar {
    width: 3px;
    height: 3px;
    border-left: 0;
    background: rgba(0, 0, 0, 0.1);
}
  
::-webkit-scrollbar-track {
    background: none;
}
  
::-webkit-scrollbar-thumb {
    background: #00853e;
    border-radius: 0;
}
  
.clearfix { display: block; }
  
.clearfix::after {
    content: '.';
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
}

/* Marker tweaks */
.mapboxgl-popup-close-button {
    display: none;
}
  
.mapboxgl-popup-content {
    font: 400 15px/22px 'Source Sans Pro', 'Helvetica Neue', Sans-serif;
    padding: 0;
    width: 180px;
}
  
.mapboxgl-popup-content-wrapper {
    padding: 1%;
}
  
.mapboxgl-popup-content h3 {
    background: #91c949;
    color: #fff;
    margin: 0;
    display: block;
    padding: 10px;
    border-radius: 3px 3px 0 0;
    font-weight: 700;
    margin-top: -15px;
}
  
.mapboxgl-popup-content h4 {
    margin: 0;
    display: block;
    padding: 10px;
    font-weight: 400;
}
  
.mapboxgl-popup-content div {
    padding: 10px;
}
  
.mapboxgl-container .leaflet-marker-icon {
    cursor: pointer;
}
  
.mapboxgl-popup-anchor-top > .mapboxgl-popup-content {
    margin-top: 15px;
}
  
.mapboxgl-popup-anchor-top > .mapboxgl-popup-tip {
    border-bottom-color: #91c949;
}


